import React from 'react';
import connect from '@vkontakte/vkui-connect';

import { Panel, View, PanelHeader, Epic, Tabbar, TabbarItem, Div, Header} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Info from './panels/Info';
import Shop from './panels/Shop';

import Icon28User from '@vkontakte/icons/dist/28/user';
import Icon28Market from '@vkontakte/icons/dist/24/market';
import Icon28Messages from '@vkontakte/icons/dist/28/messages';
import Icon28Favorite from '@vkontakte/icons/dist/28/favorite';
import Icon28More from '@vkontakte/icons/dist/28/more';


class App extends React.Component {
    constructor(props) {
        super(props);
        let url_params = this.getUrlParams(true, []);
        this.state={
            activePanel: (url_params.page != undefined ? url_params.page : 'home'),
            fetchedUser: null,
            popout: null,
            activeStory: (url_params.page != undefined ? url_params.page : 'home'),
            url_params: url_params,
            item_id: (url_params.id != undefined ? url_params.id : 0)
        }

        this.onStoryChange = this.onStoryChange.bind(this);
    }

    // Получаем параметры из УРЛа
    getUrlParams(readHash, exclude) {
        var params = Object.create(null);
        let url_params = window.location.href.split('#')[0].split('?')[1].split('&').map((n) => n.split('='));
        url_params.forEach((n) => {
            if(n[0].substr(0,3) == 'vk_' || n[0] == 'sign') {
                params[n[0]] = n[1];
            }
        });

        if(readHash && window.location.href.split('#')[1] != undefined) {
            // # хеш-параметры забираем все, что есть
            url_params = window.location.href.split('#')[1].split('&').map((n) => n.split('='));
            url_params.forEach((n) => {
                console.log(n[0], params[n[0]]);
                if(exclude.indexOf(n[0]) == -1) {
                    params[n[0]] = n[1];
                }
            });
        }

        return params;
    }

    // Эти параметры нужны для подписи AJAX-запросов
    signParams(exclude) {
        let sign_params = [];
        let params = this.getUrlParams(true, exclude);

        if(typeof(params) == 'object') {
            let keys = Object.keys(params);
            keys.map((e) => {
                sign_params.push(e + '=' + params[e]);
            });
        }
        return sign_params.join('&');
    }

    onStoryChange (e) {
        this.setState({ activeStory: e.currentTarget.dataset.story })
    }

    loadData(type) {
        const requestAddress = 'http://activeusers.ru/app_api.php';
        fetch(requestAddress).then(results => { return results.json() }).then(data => {
            let newState = {};
            newState[type] = data;
            this.setState(newState);
        }).catch(() => {
            alert('Ошибка!');
        });
    }

    componentDidMount () {
        connect.subscribe((e) => {
            switch (e.detail.type) {
                case 'VKWebAppGetUserInfoResult':
                    this.setState({ fetchedUser: e.detail.data });
                    break;
                case 'VKWebAppAccessTokenReceived':
                    this.setState({ authToken : e.detail.data.access_token });
                    this.getItems()
                    break;
                default:
                    console.log(e.detail.type);
            }
        });
        connect.send('VKWebAppGetUserInfo', {});
        //connect.send("VKWebAppGetAuthToken", {"app_id": 6906999, "scope": "market"});
        //connect.send("VKWebAppGetUserInfo", {}).then(e => this.setState({fetchedUser: e.data}))
    }

    go = e => {
    	this.setState({activePanel: e.currentTarget.dataset.to})
    }

    /*
    render() {
        return (
            <View popout={this.state.popout} activePanel={this.state.activePanel}>
    			<Home id="home" go={this.go} appState={this.state} fetchedUser={this.state.fetchedUser}/>
    			<Info id="info" go={this.go}/>
			</View>
        )
    }
    */

    render () {
        if(this.state.url_params.group_id == undefined) {
            ///let location = window.top.location;
            return (
                <View id="discover" activePanel="discover">
                    <Panel id="discover">

                        <PanelHeader>Ошибка</PanelHeader>
                        <Div>
                            Это приложение может выполняться только в контексте сообщества. Пожалуйста, допишите параметр #group_id=ID, заменив ID на порядковый номер собщества.
                        </Div>
                    </Panel>
                </View>
            )
        }

        return (
            <Epic activeStory={this.state.activeStory} tabbar={
                <Tabbar>
                  <TabbarItem
                    onClick={this.onStoryChange}
                    selected={this.state.activeStory === 'home'}
                    data-story="home"
                    text="Профиль"
                  ><Icon28User /></TabbarItem>
                  <TabbarItem
                    onClick={this.onStoryChange}
                    selected={this.state.activeStory === 'shop'}
                    data-story="shop"
                    text="Каталог"
                  ><Icon28Market /></TabbarItem>

                </Tabbar>
              }>
                <Home id="home" t={this} go={this.go} appState={this.state} fetchedUser={this.state.fetchedUser}/>
                <Shop id="shop" t={this} go={this.go} appState={this.state} fetchedUser={this.state.fetchedUser}/>

                <View id="messages" activePanel="messages">
                    <Panel id="messages">
                        <PanelHeader>Messages</PanelHeader>
                    </Panel>
                </View>
                <View id="notifications" activePanel="notifications">
                    <Panel id="notifications">
                        <PanelHeader>Notifications</PanelHeader>
                    </Panel>
                </View>
                <View id="more" activePanel="more">
                    <Panel id="more">
                        <PanelHeader>More</PanelHeader>
                    </Panel>
                </View>
            </Epic>
        )
    }
}

export default App;

