import React from 'react';
import { View, Panel, Select, Input, FormLayout, Alert, HeaderButton, PanelHeader, Group, Div, Button, List, Avatar, ListItem, Tabs, TabsItem, FixedLayout, HorizontalScroll, InfoRow, Cell, platform, IOS} from '@vkontakte/vkui';
import fetchJsonp from 'fetch-jsonp';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();

class Shop extends React.Component {

    constructor(props) {
        super(props);

        if(props.t.state.item_id > 0) {
            this.state={
                popout: null,
                res: {},
                cat: {},
                catres: [],
                cab_id: 0,
                activeTab: 0,
                activePanel: 'item',
                activeRes: props.t.state.item_id,
                activeResInfo: {}
            }

            this.getItemInfo(props.t.state.item_id);
        } else {
            this.state={
                popout: null,
                res: {},
                cat: {},
                catres: [],
                cab_id: 0,
                activeTab: 0,
                activePanel: props.id,
                activeRes: 0,
                activeResInfo: {},
                paySum: 0,
                paysys: [],
            }
        }

        this.getItems = this.getItems.bind(this);
        this.buyItem = this.buyItem.bind(this);
        this.closePopout = this.closePopout.bind(this);
    }

    buyItem(res_id, way) {
        const api = `https://activeusers.ru/miniapi.php?mode=a_buy_res&id=` + res_id + `&m=` + way + `&` + this.props.t.signParams(['id']);
        fetchJsonp(api)
            .then(res => res.json())
            .then(data => {
                if(data.data.pay !== undefined && data.data.pay > 0) {
                    this.setState({
                        activePanel: 'pay',
                        paySum: data.data.pay,
                        paysys: data.data.paysys
                    })
                } else {
                    this.openDefault(data.data.title, data.data.text, data.data.buttons)
                }
            })
            .catch(e => [])
    }

    getItemInfo(id) {
        const api = `https://activeusers.ru/miniapi.php?mode=shop_item&id=` + id + `&` + this.props.t.signParams(['id']);
        fetchJsonp(api)
            .then(res => res.json())
            .then(data => this.setState({
                activeResInfo: data.data.res,
                cab_id: data.data.cab_id,
            }))
            .catch(e => [])
    }

    createMarkup(html) {
        return {__html: html};
    }

    getItems() {
        const api = `https://activeusers.ru/miniapi.php?mode=shop&` + this.props.t.signParams(['id']);
        fetchJsonp(api)
            .then(res => res.json())
            .then(data => this.setState({
                cat: data.data.cat,
                res : data.data.res,
                catres : data.data.catres,
                cab_id: data.data.cab_id,
                activeTab: (data.data.catres[0] === undefined ? 0 : data.data.catres[0].i)
            }))
            .catch(e => [])
    }

    openDefault (title, text, buttons) {
        this.setState({
            popout:
                <Alert
                    actions={buttons}
                    onClose={this.closePopout}
                >
                    <h2>{title}</h2>
                    <p>{text}</p>
                </Alert>
        });
    }

    closePopout () {
        this.setState({ popout: null });
    }

    componentDidMount () {
        this.getItems();
    }

    render() {
        let props = this.props;
        let ar = this.state.activeResInfo;
        let item_icon = 'https://activeusers.ru/upload/cab/' + this.state.cab_id + '/' + ar.icon;
        let item_desc = typeof(ar.desc) === 'string' ? ar.desc.replace(/([^>\r\n]?)(\r\n|\n\r|\r|\n)/g, "$1<br>$2") : '';
        return (
            <View id={props.id} activePanel={this.state.activePanel} popout={this.state.popout}>
                <Panel id={props.id}>
                    <PanelHeader>Каталог</PanelHeader>
                    <HorizontalScroll>
                        <Tabs>
                            {this.state.catres.length > 0 && this.state.catres.map((e, index) => (
                                <TabsItem
                                    key={e.i}
                                    onClick={() => this.setState({activeTab : e.i})}
                                    selected={this.state.activeTab === e.i}
                                    >
                                    {this.state.cat[e.i].t}
                                </TabsItem>
                            ))
                            }
                        </Tabs>
                    </HorizontalScroll>

                    {this.state.catres.length > 0 && this.state.catres.map((e, index) => {
                        if(this.state.activeTab !== e.i) return false;
                        return (
                            <List>
                                {
                                    e.r !== undefined && e.r.length > 0 && e.r.map((res_id, index2) => {
                                        let item = this.state.res[res_id];
                                        let icon = 'https://activeusers.ru/upload/cab/' + this.state.cab_id + '/' + item.m;
                                        return (
                                            <Cell key={index2}
                                                before={
                                                    <img
                                                        alt=""
                                                        style={{
                                                            width: 40,
                                                            height : 40,
                                                            margin : 10
                                                        }}
                                                        src={icon}
                                                        />
                                                    }
                                                multiline
                                                onClick={() => {
                                                    this.setState({activePanel : 'item', activeRes: res_id})
                                                    this.getItemInfo(res_id)
                                                }}
                                            >
                                                {item.t}
                                            </Cell>
                                        )
                                    })
                                }
                            </List>
                        )})
                    }

                </Panel>
                <Panel id="item">
                    <PanelHeader left={<HeaderButton onClick={() => this.setState({activePanel : 'shop'})}>
                        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
                        </HeaderButton>}
                    >
                        {this.state.res[this.state.activeRes] !== undefined && this.state.res[this.state.activeRes].t}

                    </PanelHeader>

                    {this.state.res[this.state.activeRes] !== undefined &&
                        <>
                            <Div>
                            <Cell before={ <img
                                src={item_icon}
                                alt=""
                                style={{
                                    width: 100,
                                    height : 100,
                                    marginLeft : -10,
                                    marginRight : 10
                                }} />}>
                                <div dangerouslySetInnerHTML={this.createMarkup(item_desc)}></div>
                            </Cell>

                                {ar.get_methods !== undefined
                                    && ar.get_methods.length > 0
                                    && ar.get_methods.map(
                                        (e, index) => (
                                            <Group>{e.need.map(
                                                (r) => (
                                                    <Cell>{r.c} {this.state.res[r.r] !== undefined ? r.t : 'Ресурс #' + r.r} </Cell>
                                                )
                                            )}
                                                <Div>
                                                    <Button size="l" level={e.can_get ? 'commerce' : 'secondary'} onClick={() => this.buyItem(ar.id, index)}>Купить</Button>
                                                </Div>
                                            </Group>
                                        )
                                    )
                                }
                            </Div>
                        </>
                    }

                </Panel>
                <Panel id="pay">
                    <PanelHeader left={<HeaderButton onClick={() => this.setState({activePanel : 'item'})}>
                        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
                        </HeaderButton>}
                        >
                        Платёжная система
                    </PanelHeader>


                    {this.state.paysys !== undefined
                        && this.state.paysys.length > 0
                        && this.state.paysys.map((e, index) => {

                        if(e.type === 0) {
                            return (

                                <form method="get" ref="form" action="https://activeusers.ru/yandex_money" id="payform" target="_top">

                                        {Object.keys(e).map((k) => {
                                            if(k == 'paymentType') {
                                                return (
                                                    <Select top="Откуда" name={k} placeholder="Выберите тип" value={e[k]}>
                                                        <option value="PC">Кошелёк Яндекc-денег</option>
                                                        <option value="AC">Банковская карта</option>
                                                    </Select>
                                                )
                                            }
                                            if(k == 'sum') {
                                                return (<Input top="Сумма" type="text" value={e[k]} name={k} />)
                                            }
                                        })}
                                        <Button size="l" level="commerce" onClick={() => {this.refs.form.submit()}}>Оплатить</Button>

                                    {Object.keys(e).map((k) => {
                                        if(k !== 'sum' && k !== 'paymentType') {
                                            return (<input type="hidden" name={k} value={e[k]} />)
                                        }
                                    })}
                                </form>
                            )
                        }

                        if(e.type === 2) {

                        }
                    })
                    }

                </Panel>
            </View>
        );
    }
}
export default Shop;